<template>
  <div class="photo">
    <van-sticky>
      <van-nav-bar title="" left-text="" left-arrow @click-left="pageBack" />
    </van-sticky>
    <div class="content">
      <div class="photo-content">
        <img id="photo" class="video" v-show="isPhoto" />
      </div>
    </div>
    <div class="btns">
      <div class="btn" v-if="isPhoto" @click="isPhoto = false"></div>
      <div class="btn" v-else @click="takePhoto()"></div>
      <div class="btn" v-if="isPhoto" @click="uploadPhoto()"></div>
    </div>
    <canvas id="canvas" style="display:none;"></canvas>
  </div>
</template>
<script>
import { mixin } from "@/mixin/mixin";
import axios from "axios";
const sha1 = require("sha1");
export default {
  name: "photo",
  mixins: [mixin],
  data() {
    return {
      isPhoto: false,
      photo: "",
      id: ""
    };
  },
  mounted() {
    axios
      .get(
        "https://api.weixin.qq.com/cgi-bin/token?grant_type=client_credential&appid=wx66c7494423ceb027&secret=34fa9ffd9707f65f41989cff105f0451"
      )
      .then(res => {
        let token = res.data.access_token;
        axios
          .get(
            `https://api.weixin.qq.com/cgi-bin/ticket/getticket?access_token=${token}&type=jsapi`
          )
          .then(res => {
            let jsapi_ticket = res.data.ticket;
            console.log("jsapi_ticket: ", jsapi_ticket);
            let noncestr = "jiahua";
            let timestamp = Date.parse(new Date()) / 1000;
            let url = "http://localhost:8080";
            let str = `jsapi_ticket=${jsapi_ticket}&noncestr=${noncestr}&timestamp=${timestamp}&url=${url}`; //
            console.log("str: ", str);
            let sha = sha1(str); //
            console.log("sha: ", sha);
            wx.config({
              debug: true, // ,apialertpclogpc
              appId: "wx66c7494423ceb027", // 
              timestamp: timestamp, // 
              nonceStr: noncestr, // 
              signature: sha, // 
              jsApiList: ["chooseImage"] // JS
            });
          });
      });
    this.id = this.$route.params.id;
  },
  methods: {
    // 
    takePhoto() {
      wx.chooseImage({
        count: 1, // 9
        sizeType: ["original", "compressed"], // 
        sourceType: ["camera"], // 
        success: function(res) {
          var localIds = res.localIds; // IDlocalIdimgsrc
        }
      });
    },
    // 
    uploadPhoto() {
      this.$api.page_5
        .saveHtZxksPxzxRen({
          __sid: localStorage.getItem("JiaHuaSessionId"),
          id: this.id,
          qiandao: "1",
          qiandaoTime: new Date(),
          photo: this.photo
        })
        .then(res => {
          console.log(res);
        });
    }
  }
};
</script>
